<template>
  <div id="app">
    <div id="pdfDom">
      <h4>识别测试</h4>
      // ... 需要打印的内容
      <vue-esign
        ref="esign"
        :height="300"
        :isCrop="isCrop"
        :lineWidth="lineWidth"
        :lineColor="lineColor"
        :bgColor="bgColor"
      />
    </div>
    <div class="preview-content-operateBtn">
      <button class="previewBtn" @click="onClickDownLoad">下载PDF简历</button>
      <button @click="handleReset">清空画板</button>
      <button @click="handleGenerate">生成图片</button>
    </div>
    <h3>请一项一项识别哦,这里不会保存您的任何信息，可放心测试</h3>
    <div style='display:flex;justify-content: center;'>
    <table border='1'>
      <tr>
        <th>识别种类</th>
        <th>选择图片</th>
        <th>上传图片</th>   
        <th>识别后的数据</th>
      </tr>
      <tr>
        <td>身份证(正面)</td>
        <td><input type="file" @change="onChange" accept="image/*"></td>
        <td><button @click=update(postUrl,opCode.idCard)>上传</button></td>
        <td>扫描的身份信息:{{idCardData}}</td>
      </tr>
      <tr>
        <td>银行卡(正面)</td>
        <td><input type="file" @change="onChange" accept="image/*"></td>
        <td><button @click=update(postUrl,opCode.bankCard)>上传</button></td>
        <td>银行卡信息:{{bankCardData}}</td>
      </tr>
    </table>
    </div>
  </div>
</template>

<script>
import htmlToPdf from "./utils/htmlToPdf.js";
export default {
  name: "App",
  components: {},
  data() {
    return {
      lineWidth: 6,
      lineColor: "#000000",
      bgColor: "",
      resultImg: "",
      isCrop: false,
      htmlTitle: "页面导出PDF文件名",
      imgBase64:'',
      imgsrc:null,
      idCardData:null,
      bankCardData:null,
      opCode:{idCard:'idCard',bankCard:'bankCard'},
      postUrl:'/AIcheck'
    };
  },
  methods: {
    onClickDownLoad() {
      htmlToPdf.getPdf("下载名称");
    },
    handleReset() {
      this.$refs.esign.reset(); //清空画布
    },
    handleGenerate() {
      this.$refs.esign
        .generate()
        .then((res) => {
          this.resultImg = res; // 得到了签字生成的base64图片
        })
        .catch((err) => {
          //  没有签名，点击生成图片时调用
          this.$message({
            message: err + " 未签名!",
            type: "warning",
          });
          alert(err); // 画布没有签字时会执行这里 'Not Signned'
        });
    },
    axios_post(url,data){
      return new Promise(
      (resolve,reject)=>{
      this.axios.post(url,data)
          .then(res=>{
            resolve(res.data)
          }).catch(err=>{
          reject(err.data)
        })
      }
    )
    },
    update(url,opCode){
      let _this=this
      let data={imgBase64:this.imgBase64,op_code:opCode}
      let res=_this.axios_post(url,data)
      res.then(res=>{
         console.log(res)
      if(opCode=='idCard'){
        _this.idCardData=res
        }

        
        if(opCode=='bankCard'){
          _this.bankCardData=res
        }
      })
    },
    //改变后直接压缩
    onChange(event){ 
      let _this=this
      let file=event.target.files[0]
      let reader = new FileReader();
      reader.readAsDataURL(file);
      let img=_this.changeBase64(reader)
      img.then(res=>{
      console.log(res)
      _this.imgBase64=res
    })
  },
  //装换成base64格式
  changeBase64(reader){
      return new Promise(
      (resolve)=>{
      reader.onload = function () {
      let result = reader.result; //获取到base64格式图片
      resolve(result)
      }
      })
  }
  },

  mounted(){

  }
};
</script>

